.qt-box-text {
    color: white;
    font-size: 250%;
    padding: 5%;
}

.qt-box-author {
    color: white;
    font-size: 150%;
    padding: 5% 5% 0%;
}

.qt-box-column {
  float: center;
  width: 100%;
  margin: 1%;
}

.qt-box-qt-row:after {
  content: "";
  display: table;
  clear: both;
}
/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .qt-box-column.side, .qt-box-column.middle {
    width: 100%;
  }
}