
.qt-header-title {
    padding-top: 1%;
    font-size: 300%;
    color: rgb(0, 184, 113);
}

.qt-header-subtitle {
  font-size: 200%;
  padding-bottom: 2%;
  color: rgb(0, 184, 113);
}

/* Create three unequal columns that floats next to each other */
.qt-header-column {
  float: left;
  padding: 10px;
  text-align: center;
}

/* Left and right column */
.qt-header-column.side {
  width: 25%;
}

/* Middle column */
.qt-header-column.middle {
  width: 50%;
}

/* Clear floats after the columns */
.qt-header-row:after {
  content: "";
  display: table;
  clear: both;
}
/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .qt-header-column.side, .qt-header-column.middle {
    width: 100%;
  }
}